const Papa = require('Papaparse');


// return jsonData
function readCsv(file){
  const config = {
    headers: true
  }
  const json = Papa.parse(file, config);
  console.log(json);
}

// parse source, return array of clean records
function transformer(json, descAlias, costAlias){ // alias = file column names for procedureName, etc

  const recordTemplate = {
    hospital: '',
    procedureName: '',
    procedureCost: ''
  }
  var record = {...recordTemplate}; // clone
  // look through source column names, get relevant data for us
  for (let prop in json) {
    console.log(`${prop}: ${json[prop]}`); // "DataType: value"
    if (prop == descAlias) {
      record["procedureName"] = json[prop];
    } else if (prop == costAlias) {
      record["procedureCost"] = json[prop];
    }
  }

  //console.log(record);

}
module.exports.readCsv = readCsv;
